t-shadow:0 0 3px #444}.movie-card-holder .movie-card__desc{font-weight:300;opacity:.84;margin-bottom:2em}h1,h2,h3{font-family:"Montserrat",helvetica,arial,sans-serif;text-transform:uppercase;letter-spacing:2px;line-height:1;font-weight:400}.btn{padding:.5rem 2rem;background-color:rgba(255,255,255,0.4);color:#fff}.btn-outline{background-color:transparent;border:3px solid #ffffff}.btn::before{vertical-align:middle;font-size:1.5em;padding-right:.5em}.btn-outline:hover{border-color:#2a9fff;color:#2a9fff;box-shadow:0px 1px 8px 0px rgba(245,199,0,0.2)}.img-fluid{-webkit-filter:drop-shadow(1px 3px 1px #000);filter:drop-shadow(1px 3px 1px #000)}.see-link{font-size:1.3rem;text-shadow:0 3px 5px #444}

