.registration-holder div{
    border:none;
}

.registration-form{
    padding: 50px 0;
    border:none;
}



.registration-form form{
    background-color: #fff;
    max-width: 600px;
    margin: auto;
    padding: 50px 70px;
    border-top-left-radius: 30px;
    border-top-right-radius: 30px;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.375);
}

.registration-form .me-icon{
	text-align: center;
    border-radius: 50%;
    font-size: 40px;
    color: white;
    width: 100px;
    height: 100px;
    margin: auto;
    margin-bottom: 50px;
    line-height: 100px;
}

.registration-form .item{
	border-radius: 20px;
    margin-bottom: 25px;
    padding: 10px 20px;
}

.registration-form .send2me{
    border-radius: 30px;
    padding: 10px 20px;
    font-size: 18px;
    font-weight: bold;
    background-color: #5791ff;
    border: none;
    color: white;
    margin-top: 20px;
    box-shadow:0 1.5px 10px #50999f;
    transition:.5s ease-in-out;

}

.registration-form .send2me:hover{
    box-shadow:0 0 1px #000;

}

.registration-form .social-media{
    max-width: 600px;
    background-color: #fff;
    margin: auto;
    padding: 35px 0;
    text-align: center;
    border-bottom-left-radius: 30px;
    border-bottom-right-radius: 30px;
    color: #9fadca;
    border-top: 1px solid #dee9ff;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.075);
}

.registration-form .social-icons{
    margin-top: 8px 0px;
}

.registration-form .social-icons a{
    font-size: 25px;
    margin: 0 10px;
    border: 1px solid;
    border-radius: 50%;
    width: 45px;
    display: inline-block;
    height: 45px;
    text-align: center;
    background-color: #fff;
    line-height: 45px;
    box-shadow:0 0 6px #50999f;
}

.registration-form .social-icons a:hover{
    text-decoration: none;
    opacity: 0.6;
}




.con-head, .con-head small{
	width:100%;
	display:flex;
	justify-content:space-between;

}


.con-head small span{
	flex:3;

}

.con-head small sub{
	flex:1;

}




@media (max-width: 576px) {
    .registration-form form{
        padding: 50px 20px;
    }

    .registration-form .form-icon{
        width: 70px;
        height: 70px;
        font-size: 30px;
        line-height: 70px;
    }
}